<template>
  <div class="taxDeduction">
    <p class="taxDeduction-title">แผนลดหย่อนภาษี</p>
    <p class="annual-income">
      <span>รายได้ต่อปี</span>
      <aia-input type="number" v-model="annualIncome" @input="changeAnnual"></aia-input>
    </p>
    <div class="baseMain">
      <div>
        <p><b>1. ประกันชีวิต + ประกันสุขภาพของตนเอง</b> (ไม่เกิน 15,000 บาท รวมกันไม่เกิน 100,000 บาท)</p>
        <p class="specific_main">
          <span>คุณใช้สิทธิ์ลดหย่อนภาษีด้วยผลิตภัณฑ์ประกันชีวิตไปแล้วเท่าไร?</span>
          <aia-input type="number" v-model="LifeinsuranceOwninsuranceCurrent"></aia-input>
        </p>
        <p class="specific_border_main">
          <span>สามารถใช้สิทธิ์ลดหย่อนได้อีก</span>
          <span class="borderLine textRed">{{ LifeinsuranceOwninsuranceMore }}</span>
        </p>
      </div>
      <div>
        <p><b>2.ประกันชีวิตคู่สมรสที่ไม่มีเงินได้</b> (ไม่เกิน 10,000 บาท)</p>
        <p class="specific_main">
          <span>คุณใช้สิทธิ์ลดหย่อนภาษีด้วยผลิตภัณฑ์ประกันชีวิตไปแล้วเท่าไร?</span>
          <aia-input type="number" v-model="LifeinsuranceSpousesCurrent"></aia-input>
        </p>
        <p class="specific_border_main">
          <span>สามารถใช้สิทธิ์ลดหย่อนได้อีก</span>
          <span class="borderLine textRed">{{ LifeinsuranceSpousesMore }}</span>
        </p>
      </div>
      <div>
        <p><b>3.ประกันสุขภาพของบิดามารดา และบิดามารดาคู่สมรสที่ไม่มีเงินได้</b> (ไม่เกิน 15,000 บาท)</p>
        <p class="specific_main">
          <span>คุณใช้สิทธิ์ลดหย่อนภาษีด้วยผลิตภัณฑ์ประกันชีวิตไปแล้วเท่าไร?</span>
          <aia-input type="number" v-model="healthInsuranceCurrent"></aia-input>
        </p>
        <p class="specific_border_main">
          <span>สามารถใช้สิทธิ์ลดหย่อนได้อีก</span>
          <span class="borderLine textRed">{{ healthInsuranceMore }}</span>
        </p>
      </div>
      <div>
        <p><b>4. ประกันบำนาญของตนเอง</b> (ไม่เกิน 200,000 บาท และ 15% ของเงินได้)</p>
        <p class="specific_main">
          <span>คุณใช้สิทธิ์ลดหย่อนภาษีด้วยผลิตภัณฑ์ประกันชีวิตไปแล้วเท่าไร?</span>
          <aia-input type="number" :disabled="ownPensionInsuranceDis" v-model="ownPensionInsuranceCurrent"></aia-input>
        </p>
        <p class="specific_border_main">
          <span>สามารถใช้สิทธิ์ลดหย่อนได้อีก</span>
          <span class="borderLine textRed">{{ ownPensionInsuranceMore }}</span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'taxDeduction',
  data(){
    return {
      annualIncome:'',
      LifeinsuranceOwninsuranceCurrent:'',
      LifeinsuranceSpousesCurrent:'',
      healthInsuranceCurrent:'',
      ownPensionInsuranceCurrent:'',
      ownPensionInsuranceDis:true
    }
  },
  methods:{
    changeAnnual(){
      if(this.annualIncome != ''){
        this.ownPensionInsuranceDis = false;
      }else{
        this.ownPensionInsuranceDis = true;
      }
    }
  },
  computed:{
    LifeinsuranceOwninsuranceMore(){
      if(this.LifeinsuranceOwninsuranceCurrent == ''){
        return '';
      }else{
        if(100000 - this.LifeinsuranceOwninsuranceCurrent < 0){
          return 0;
        }else{
          return 100000 - this.LifeinsuranceOwninsuranceCurrent
        }
      }
    },
    LifeinsuranceSpousesMore(){
      if(this.LifeinsuranceSpousesCurrent == ''){
        return '';
      }else{
        if(100000 - this.LifeinsuranceSpousesCurrent < 0){
          return 0;
        }else{
          return 100000 - this.LifeinsuranceSpousesCurrent
        }
      }
    },
    healthInsuranceMore(){
      if(this.healthInsuranceCurrent == ''){
        return '';
      }else{
        if(15000 - this.healthInsuranceCurrent < 0){
          return 0;
        }else{
          return 15000 - this.healthInsuranceCurrent
        }
      }
    },
    ownPensionInsuranceMore(){
      let ownPensionInsuranceMore = (this.annualIncome * 0.15 ) - this.ownPensionInsuranceCurrent;
      if(ownPensionInsuranceMore < 0){
        return 0;
      }
      return ownPensionInsuranceMore;      
    }
  }
};
</script>
